import jsonp from 'jsonp';
import axios from 'axios';
import AxiosCustomed from '../axios/axiosCustomed';
import config from '../config';

debugger

export default () => {
  return new Promise((resolve) => {
    jsonp(`${config.domain}/api/jsonp/getCookie.js?callback=`, {
      prefix: 'liveJSONP',
    }, (err, data) => {
      console.log(data)
      debugger
      if (err || (data && typeof data === 'object' && !Object.keys(data).length)) {
        // redirect()
        return false;
      }
      axios.get(`http://test.live.videojj.com/api/users/auth/${data.token}`)
        .then(response => ({
          data: response.data,
          status: response.status,
        }))
        .then(({ data, status }) => {
          if (status !== 200 || data.status !== 0) {
            // redirect()
            return false;
          }
          AxiosCustomed.setToken(data.result.token);
          // return getUserInfo();
        })
        .then(userInfo => {
          // storage.setPlatformUserInfo(userInfo)
          resolve();
        });
    });
  });
};



